import * as React from "react";
import PropTypes from "prop-types";
import { DefaultButton } from "@fluentui/react";
import Progress from "./Progress";

export default class App extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      subject: `Indeed Interview with John Smith`,
      location: `Indeed interview link`,
      attendees: [`john.smith@example.com`],
      added: false,
    };
  }

  click = async (e) => {
    e.preventDefault();
    const { item } = Office.context.mailbox;

    item.notificationMessages.replaceAsync("action", {
      type: Office.MailboxEnums.ItemNotificationMessageType.InformationalMessage,
      message: "Adding interview",
      icon: "Icon.80x80",
      persistent: true,
    });

    item.subject.setAsync(this.state.subject);
    item.location.setAsync(this.state.location);
    item.requiredAttendees.setAsync(this.state.attendees);
    item.body.setAsync(`<b>Indeed Interview</b> <br /><br /> Interviewee: John Smith`, {
      coercionType: Office.CoercionType.Html,
    });
    this.setState({ added: true });

    item.notificationMessages.replaceAsync("action", {
      type: Office.MailboxEnums.ItemNotificationMessageType.InformationalMessage,
      message: "Indeed Interview added",
      icon: "Icon.80x80",
      persistent: false,
    });
  };

  render() {
    const { isOfficeInitialized } = this.props;

    if (!isOfficeInitialized) {
      return <Progress message="Please sideload your addin to see app body." />;
    }

    return (
      <div className="ms-welcome">
        <div className="ms-welcome--placeholder">
          <div>Subject: {this.state.subject}</div>
          <div>Location(link): {this.state.location}</div>
          <div>Attendees: {this.state.attendees[0]}</div>
        </div>
        <div className="ms-welcome--placeholder">Other things we need to fetch here</div>
        <DefaultButton
          disabled={this.state.added}
          type="submit"
          className="ms-welcome__action"
          iconProps={{ iconName: "ChevronRight" }}
          onClick={this.click}
        >
          {this.state.added ? `Added` : `Add interview info`}
        </DefaultButton>
      </div>
    );
  }
}

App.propTypes = {
  title: PropTypes.string,
  isOfficeInitialized: PropTypes.bool,
};
